@import './../color-vars.scss';

.content {
  width: 94%;
  padding: 40px;
  text-align:center;

  &__no-data {
    width: 42%;
    height: 500px;
    background-repeat: no-repeat;
    margin: 0 auto;
    background-size: contain;
    background-image: url('./../assets/icons/no-data.jpg');

    @media only screen and (max-width: 720px) {
      height: 200px;
    }

  }

  &__message {
    margin-right: 7%;
    margin-top: 0;
    text-align: center;
    font-size: 20px;
    color: $primary;
  }

  &__button {
    background-color: $primary;
    border: 2px solid $primary;
    color:white;
    padding: 15px 32px;
    text-align: center;
    display: block;
    font-size: 16px;
    margin: 0 42%;
    cursor: pointer;
    transition: 0.2s;

    @media only screen and (max-width: 720px) {
      margin: 0 30%;
      width:40%;
    }

    &:hover {
      background-color: white;
      color: $primary;
    }
  }
}
